import React from 'react';

class Items extends React.PureComponent {
  render() {
    let items;

    if (this.props.items) {
      items = this.props.items.slice();

      if (!this.props.isAsc) {
        items.reverse();
      }

      if (this.props.title) {
        items = items.filter(item => {
          return item.title.indexOf(this.props.title) > -1;
        });
      }

      if (this.props.dateStart.length === 8) {
        const dateStart = new Date(this.props.dateStart);

        items = items.filter(item => {
          const itemDate = new Date(item.date.substr(0, 10));

          return itemDate >= dateStart;
        });
      }

      if (this.props.dateEnd.length === 8) {
        const dateEnd = new Date(this.props.dateEnd);

        items = items.filter(item => {
          const itemDate = new Date(item.date.substr(0, 10));

          return itemDate <= dateEnd;
        });
      }

      if (!this.props.showDuplicates) {
        items = items.filter(item => item.isFirst);
      }

      if (!this.props.showBooks) {
        items = items.filter(item => item.type !== 'book');
      }

      if (!this.props.showComics) {
        items = items.filter(item => item.type !== 'comic');
      }

      if (!this.props.showMovies) {
        items = items.filter(item => item.type !== 'movie');
      }

      items = items.map((item, index) => {
        const date  = new Date(item.date.replace(' ', 'T'));

        // timezone not accounted for in SQL
        date.setHours(date.getHours() + 5);

        const month = (date.getMonth() + 1).toString().padStart(2, '0');
        const day   = date.getDate().toString().padStart(2, '0');
        const year  = date.getFullYear().toString().substr(2);
        const title = item.isFirst ? item.title : `[${item.title}]`;
        const id    = this.props.isAsc ? index + 1 : items.length - index;
        const date6 = (this.props.showDate) ? ` (${month}/${day}/${year})` : '';
        let   icon;

        switch (item.type) {
          default:
          case 'book':
            icon = (<i aria-hidden={true} className="mr5 fas fa-book"/>);
            break;
          case 'comic':
            icon = (<i aria-hidden={true} className="mr5 fas fa-book-open"/>);
            break;
          case 'movie':
            icon = (<i aria-hidden={true} className="mr5 fas fa-film"/>);
            break;
        }

        return (
          <div key={index}>
            {id}. {icon}{title}{date6}
          </div>
        );
      });

      items = (items.length > 0) ? items : (<div>...</div>);
    }

    return (
      <div id="items" className="bdrBox bdrGray p5">
        {items}
      </div>
    );
  }
}

export default Items;
